export default {
  name: 'Money',
  props: {
    balance: Number
  },
  data: function data() {
    return {
      moneySymbole: ""
    };
  },
  computed: {
    balanceStyle: function balanceStyle() {
      return {
        "negative-balance": this.balance <= 0,
        "positive-balance": this.balance > 0
      };
    },
    formatBalance: function formatBalance() {
      return this.balance.toFixed(2).replace('.', ',').replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    }
  }
};